.card {
  height: 100px;
  cursor: pointer;
  line-height: 100px;
  background-color: #bae7ff;
  color: #333;
  font-size: 32px;
  border: 1px solid #1890ff;
  margin-bottom: 2px;
  text-align: center;
}

.card.hidden {
  cursor: auto;
  visibility: hidden;
}

.card:hover {
  background-color: #91d5ff;
}